.App {
  box-sizing:initial;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
}

.site-title{
  margin-right: auto;
  font-size: 10vh;
  font-weight: 400;
}
.cta{
  margin-left: 20px;
}

li, a, button {
  font-family:Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 20px;
  color: #515455;
  text-decoration: none;
}

.App-header {
  display:flex;
  margin: 0px 0px;
  flex-direction:initial;
  justify-content: flex-end;
  align-items:center;
  padding: 0px 4%;
}

.nav-links{
  list-style: none;
}
.nav-links li{
  display: inline-block;
  padding: 10px 30px;
}
.nav-links li a {
  transition: all 0.3s ease 0s;
}
.nav-links li a:hover {
  color:#FF4AED;
}

.App-link {
  color: aqua;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
